html {
  height: 100%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  height: 100%;
  padding: 0;
  margin: 0;
  background-color: #ffffff;
  color: #222222;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 12px;
}

#root {
  height: 100%;
}
